import { createApp } from 'vue';
import App from './App.vue';
import router from '../src/script/router';
import store from '@/store';
import 'bootstrap-icons/font/bootstrap-icons.css';

function parseJSON(value) {
  try {
    return JSON.parse(value);
  } catch (e) {
    return null;
  }
}

const storedUser = parseJSON(localStorage.getItem('user'));

const app = createApp(App);

app.use(router);
app.use(store);

// Vuexì—ì„œ ì‚¬ìš©ìž ì •ë³´ë¥¼ ì„¤ì •
if (storedUser) {
  store.dispatch('login', { user: storedUser, role: localStorage.getItem('role') || '' }); // ì‚¬ìš©ìž ë¡œê·¸ì¸ ìƒíƒœë¡œ ì„¤ì •
}

app.mount('#app');
